<template>
  <div id="app">
    <p>{{ message }}</p>
    <input v-model="message">
  </div>
</template>

<script>
    export default {
        name: "vuem-modle",
      data: function () {
        return {
          message: 'Hello Vue!'
        }
      }
    }
</script>

<style scoped>

