import Vue from 'vue'
import Router from 'vue-router'
import modle from '@/components/vuem-modle'
import test from '@/components/Testmodle'
import list from  '@/components/shoppinglist'
import login from  '@/components/login'
import resigretor from  '@/components/resigretor'
import card from  '@/components/selectorcard'
import search from  '@/components/search-panel'
import footer from  '@/pages/footer'


Vue.use(Router)

export default new Router({
  base:'/community/',
  routes: [
    {
      path: '/login',
      name: 'login',
      component: login,
      children:[
        {
          path:'',
        component:modle
        },
        {
        path: '/modle',
        name: 'modle',
        component: modle
      },
        {
          path:'/test',
          name:'test',
          component:test
        },
        {
          path:'/card',
          name:'card',
          component:card
        },
        {
          path:'/list',
          name:'list',
          component:list
        },
        {
          path: '/login',
          name: 'login',
          component: login
        },
        {
          path: '/search',
          name: 'search',
          component: search
        },
        {
          path:'/footer',
          name:'footer',
          component:footer
        }
      ]
    },
    {
      path: '/resigretor',
      name: 'resigretor',
      component: resigretor
    },

   ]
})



