<template>
  <div style="height: 100%;width: 100%">
      <swiper :options="swiperOption" style=";height: 60vh;width: 100%;">
        <swiper-slide v-for="item in images" style="border-radius: 10px"><img :src="item.src" alt=""></swiper-slide>
        <div class="swiper-pagination" slot="pagination"></div>
        <div class="swiper-button-prev" slot="button-prev"></div>
        <div class="swiper-button-next" slot="button-next"></div>
      </swiper>
  </div>
</template>

<script>

  import { swiper, swiperSlide } from 'vue-awesome-swiper'

  export default {
    name: 'slider',
    data() {
      return {
        images: [
          {'src': '/static/images/1.jpg'},
          {'src': '/static/images/2.jpg'},
          {'src': '/static/images/3.jpg'},
          {'src': '/static/images/4.jpg'},
          {'src': '/static/images/5.jpg'},
          {'src': '/static/images/6.jpg'},
        ],
        swiperOption: {
          slidesPerView: 1,
          spaceBetween: 30,
          loop: true,
          autoplay:{
            delay:2500,
            disableOnInteraction:false
          },
          pagination: {
            el: '.swiper-pagination',
            clickable: true
          },
          navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev'
          }
        }
      }
    }
  }
</script>

<style scoped>
  img {
    width: 100%;
    height: 100%;
    border-radius: 5px;
  }
.content {
  background-image: url("/static/images/1.jpg");
  background-size: 100% 100%;
  width: 100%;
  height: 500px;
}
</style>



