<template>
     <div>
      <router-link to="/modle"  class="button"> modle</router-link>
      <router-link to="/test" :class="{button:isbutton==false}">todo list</router-link>
      <router-link to="/card" class="button">选项卡</router-link>
      <router-link to="/list" :class="{active:isbutton,button:isbutton,visited:visited}">list</router-link>
       <router-link to="/search" class="button">搜索</router-link>
       <!--<router-link to="/footer" class="button">不同文件夹components</router-link>-->
       <div>  <a @click="test">变蠢按钮</a></div>
       <keep-alive><router-view/></keep-alive>
     </div>
</template>

<script>
    export default {
        name: "login",
      data:function () {
        return{
          isbutton:false,
          visited:false,
        }
      },
      methods:{
        test() {
               this.isbutton=!this.isbutton
              console.log('我点到你了吗?')
              console.log(this)
        }
      },
    }
</script>

<style scoped>
.button{
  font-family: "UD Digi Kyokasho N-R";
  font-weight: bolder;
  display: ruby;
  font-size: 50px;
}
  .active {
    text-decoration: line-through;
  }
  .visited {
    color: #9f82ff;
  }
  a {
    cursor: pointer;
  }
</style>



