<template>
    <div class="sidenav">
      <span @click="test" v-show="isActive" class="navcontrol">收起导航栏 &uparrow;</span>
      <span @click="test" v-show="!isActive" class="navcontrol">打开导航栏 </span>
      <transition name="nav">
      <ul  v-show="isActive">
        <li v-for="key in sidenavcontent">
            {{key.name}}
        </li>
      </ul>
      </transition>
    </div>
</template>

<script>
    export default {
        name: "sidenav",
      data:function () {
        return{
          isActive:true,
          sidenavcontent:[
            {'name':'猫猫之家'},
            {'name':'今日猫猫'},
            {'name':'您与猫猫'},
            {'name':'猫猫交友'},
            {'name':'猫猫配种'},
            {'name':'带猫回家'},
            ]
        }
      },
     methods:{
          test(){
            this.isActive=!this.isActive;
          }
      }
    }
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
  }
.sidenav {
  position:fixed;
  right: 0px;
  top: 25vh;
}
.navcontrol {
  margin:35px ;
  cursor: pointer;
}
  .sidenav ul {
    list-style-type:none ;
    border-width:2px 0 2px 2px;
    border-style: solid none solid solid;
    border-color: #cdcdcd;

    border-radius:20px 0 0 20px;
    padding: 10px;
  }
  .sidenav ul li{
    font-family: "UD Digi Kyokasho NK-R";
    font-size: 2em;;
    cursor: pointer;
    border: 2px solid #ffffff;

}
  .sidenav ul li:hover{
   background: #1ad1ff;

  }
  .sidenav ul li:not(:last-child) {
    border-bottom-color: #b5b5b5;
  }
  .nav-enter-active,.nav-leave-active {
    transition: opacity .5s;
  }
  .nav-enter,.nav-leave-to {
    opacity: 0;
  }
</style>



